import {
  ActivityIndicator,
  Image,
  Linking,
  PermissionsAndroid,
  Platform,
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  useWindowDimensions,
  View,
} from 'react-native';
import styles from './styles';
import {Icon, Spinner} from '@ui-kitten/components';
import TextCM from '../components/Text';
import {CommonUtils, DateUtils} from '../../utils';
import {useCallback, useEffect, useState} from 'react';
import NetInfo from '@react-native-community/netinfo';
import {useAccountInfoServices} from '../../hooks/apiHooks/useAccountInfoServices';
import ModalFullScreen from '../components/ModalFullScreen';
import CalenderDetail from './components/calender-detail';
import Geolocation, {
  GeolocationResponse,
} from '@react-native-community/geolocation';
import HeaderCM from '../components/Header/HeaderCM';
import {Color, Font} from '../../constants';
import {NetworkInfo} from 'react-native-network-info';
import {useAppSelector} from '../../hooks/useRedux';
import {getEndOfDayISO, getStartOfDayISO, isSameUTCDate} from './helper';
import Toast from 'react-native-toast-message';
import {IconCalendar, IconCheckin, IconOnsite} from '../../assets/images';
import {
  ICheckinOnsiteRecord,
  ICheckinOnsiteResponse,
  ICheckInOutRecord,
} from './interfaces';
import {useTranslation} from 'react-i18next';
import CropPickerComponent from '../components/ImageCropPicker';
import React from 'react';
import {useFocusEffect} from '@react-navigation/native';
import ModalConfirmCM from '../components/ModalConfirm';
import InfoCheckinoutDashboard from './components/info-dashboard';
import Clock from './components/clock';
import {Snackbar} from 'react-native-paper';

type Props = {
  type?: 'onsite' | 'checkinout';
  onClose?: () => void;
};

const CheckInOut = React.memo(({type, onClose}: Props) => {
  const {t} = useTranslation();
  const {width, height} = useWindowDimensions();
  const AccountServices = useAccountInfoServices();
  const [currentDate, setCurrentDate] = useState(new Date());
  const [openSnackBar, setOpenSnackBar] = useState(false);
  const [messSnack, setMessSnack] = useState('');
  const [typeSnack, setTypeSnack] = useState<'success' | 'error'>('success');
  const [showModal, setShowModal] = useState<'calender' | 'onsite'>();
  const [stateInOut, setStateInOut] = useState<{
    dataIn?: string;
    dataOut?: string;
    locationIn?: string;
    locationOut?: string;
  }>();
  const [dataLocal, setDataLocal] = useState<any>(null);
  const [ip, setIP] = useState<{
    ssid: string | null;
    ipGateway: string | null;
  }>();
  const [isLoading, setIsLoading] = useState(true);
  const {devicesInfo} = useAppSelector(state => state.common);
  const {dataAccount} = useAppSelector(state => state.account);
  const [dataCheckInOut, setDataCheckInOut] = useState<ICheckInOutRecord[]>([]);
  const [dataOnsite, setDataOnsite] = useState<ICheckinOnsiteRecord>();
  const [loadingImgIn, setLoadingImgIn] = useState(false);
  const [loadingImgOut, setLoadingImgOut] = useState(false);
  const [currentOnsiteIn, setCurrentOnsiteIn] = useState<{
    image?: string;
    location?: string;
  }>({});
  const [currentOnsiteOut, setCurrentOnsiteOut] = useState<{
    image?: string;
    location?: string;
  }>({});
  const [locationAccess, setLocationAccess] = useState(false);
  const [showConfirmOpenCamera, setShowConfirmOpenCamera] = useState(false);
  const [isFirstLoadOnsite, setIsFirstLoadOnsite] = useState(true);

  function getStartAndEndOfMonth(dateInMonth: Date) {
    const referenceDate = new Date(dateInMonth);
    const year = referenceDate.getFullYear();
    const month = referenceDate.getMonth();
    const startDate = new Date(year, month, 1, 0, 0, 0, 0);
    const firstDayOfNextMonth = new Date(year, month + 1, 1);
    const endDate = new Date(firstDayOfNextMonth.getTime() - 1);
    const formatDateToISOString = (date: Date) => {
      return date.toISOString();
    };
    const resultObject = {
      timeYear: '',
      timeMonth: '',
      timeDay: '',
      startDate: formatDateToISOString(startDate),
      endDate: formatDateToISOString(endDate),
    };
    return resultObject;
  }
  useEffect(() => {
    if (!dataCheckInOut || dataCheckInOut?.length === 0) {
      setStateInOut(undefined);
      return;
    }
    const todayIns = dataCheckInOut.filter(rec =>
      isSameUTCDate(rec?.checkinTime),
    );

    if (todayIns.length === 0) {
      setStateInOut(undefined);
      return;
    }
    const earliestIn = todayIns.reduce((prev, curr) => {
      return new Date(prev?.checkinTime!)?.getTime() <=
        new Date(curr?.checkinTime!)?.getTime()
        ? prev
        : curr;
    });

    setStateInOut({
      dataIn: earliestIn.checkinTime!,
      dataOut: earliestIn.checkoutTime ?? undefined,
      locationIn: earliestIn.locationNote ?? undefined,
      locationOut: earliestIn.locationNote ?? undefined,
    });
  }, [dataCheckInOut]);

  const getDataCheckin = async (
    d: Date = new Date(),
    workLocation: 'default' | 'onsite' | 'all' = 'all',
  ) => {
    try {
      const dataTranfer = getStartAndEndOfMonth(d);
      const res = await AccountServices.getTimeLogUser(
        dataTranfer,
        workLocation,
      );
      if (res) {
        setIsLoading(false);
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('DB.etms.errorGetDataCheckin'),
        });
        return;
      }
      setDataCheckInOut(res);
    } catch (error) {
    } finally {
      setIsLoading(false);
    }
  };

  const getDataOnsite = async (isFirst = false) => {
    try {
      isFirst && setIsFirstLoadOnsite(true);
      const dataSend = {
        startDate: getStartOfDayISO(),
        endDate: getEndOfDayISO(),
      };
      const res = (await AccountServices.getOnsiteDataByDate(
        dataSend,
      )) as ICheckinOnsiteResponse;
      if (res && res.content.length) {
        let data = res.content;
        let CIOnsiteFilter = data.filter(rq => rq.type === 'check in');
        if (CIOnsiteFilter.length) {
          const earliestIn = CIOnsiteFilter.reduce((prev, curr) => {
            return new Date(prev.checkinDate!).getTime() <=
              new Date(curr.checkinDate!).getTime()
              ? prev
              : curr;
          });
          setDataOnsite(earliestIn);
          setCurrentOnsiteIn({
            image: earliestIn.attachment,
          });
          let COOnsiteFilter = data.filter(rq => rq.type === 'check out');

          if (COOnsiteFilter.length) {
            const latestOut = COOnsiteFilter.reduce((prev, curr) => {
              return new Date(prev.checkinDate!).getTime() >=
                new Date(curr.checkinDate!).getTime()
                ? prev
                : curr;
            });
            setCurrentOnsiteOut(prev => ({
              ...prev,
              image: latestOut.attachment,
            }));
            let newOnsiteData: ICheckinOnsiteRecord = {
              ...earliestIn,
              checkoutDate: latestOut.checkinDate,
            };
            setDataOnsite(newOnsiteData);

            const locationCheckin = await AccountServices.fetchLocation({
              lon: earliestIn?.longitude,
              lat: earliestIn?.latitude,
            });
            setCurrentOnsiteIn(prev => ({
              ...prev,
              location:
                locationCheckin?.display_name ||
                `Không lấy được tên của vị trí: \n latitude: ${earliestIn.latitude} \n longitude: ${earliestIn.longitude}`,
            }));
            const locationCheckout = await AccountServices.fetchLocation({
              lon: latestOut.longitude,
              lat: latestOut.latitude,
            });
            setCurrentOnsiteOut(prev => ({
              ...prev,
              location:
                locationCheckout?.display_name ||
                `Không lấy được tên của vị trí: \n latitude: ${latestOut.latitude} \n longitude: ${latestOut.longitude}`,
            }));
          }
        }
      }
    } catch (error) {
      displaySnackbar(t('CM.error'), 'error');
    } finally {
      setIsFirstLoadOnsite(false);
    }
  };

  useFocusEffect(
    useCallback(() => {
      if (type === 'checkinout') {
        getDataCheckin();
      } else {
        getDataOnsite(true);
      }
    }, []),
  );

  useEffect(() => {
    const unsubscribe = NetInfo.addEventListener(async state => {
      if (state.isConnected) {
        const SSID = await NetworkInfo.getSSID();
        const defaultGateway = await NetworkInfo.getGatewayIPAddress();
        setIP({
          ssid: SSID,
          ipGateway: defaultGateway,
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('DB.etms.noWifiConnection'),
        });
      }
    });
    return () => unsubscribe();
  }, []);
  useEffect(() => {
    if (type === 'onsite') {
      getLocation();
    }
  }, []);

  const getLocation = async () => {
    try {
      return new Promise<GeolocationResponse>((resolve, reject) => {
        Geolocation.getCurrentPosition(
          async (position: GeolocationResponse) => {
            if (!position?.coords) return;
            const dataLocal = {
              timestamp: position.timestamp,
              latitude: position.coords?.latitude,
              longitude: position.coords?.longitude,
            };
            setDataLocal(dataLocal);
            return resolve(position);
          },
          error => {
            console.error('Error getting location:', error);
            displaySnackbar(
              'Có lỗi xảy ra khi truy cập vị trí của bạn',
              'error',
            );
            return reject(error);
          },
        );
      });
    } catch (error) {
      console.error(error);
      displaySnackbar('Có lỗi xảy ra khi truy cập vị trí của bạn', 'error');
    }
  };

  const getHeaderCheckInOut = () => {
    if (type === 'onsite') {
      return (
        <HeaderCM
          style={{
            backgroundColor: '#fff',
          }}
          contentStyle={{
            backgroundColor: '#fff',
            borderBottomWidth: 1,
            borderBottomColor: Color.borderColor,
          }}
          titleStyle={{
            color: '#000810',
          }}
          fillIconBackLeft="#000810"
          title={'OnSite'}
          onPressIconLeft={onClose}
        />
      );
    }
  };

  const getOnsite = () => {
    if (type === 'onsite') {
      return (
        <View style={{flex: 1}}>
          <ScrollView
            showsVerticalScrollIndicator={false}
            showsHorizontalScrollIndicator={false}
            style={{
              flex: 1,
            }}>
            <TextCM
              style={{
                fontStyle: 'italic',
                fontSize: 12,
                fontFamily: Font.InterMedium500,
                marginBottom: 16,
              }}>
              {t('DB.etms.descriptionCheckinOnsite')}
            </TextCM>

            {stateInOut?.locationIn && (
              <View>
                <TextCM>{t('DB.etms.checkin')}</TextCM>
                <TextCM>{JSON.stringify(stateInOut?.locationIn)}</TextCM>
              </View>
            )}

            {stateInOut?.locationOut && (
              <View>
                <TextCM>{t('DB.etms.checkout')}</TextCM>
                <TextCM>{JSON.stringify(stateInOut?.locationOut)}</TextCM>
              </View>
            )}

            {/* {location && (
            <TextCM style={{marginTop: 5}}>
              {t('DB.etms.yourLocation')} {location.display_name}
            </TextCM>
          )} */}
            {currentOnsiteIn?.image && (
              <View style={{flexDirection: 'row', gap: 10, marginBottom: 16}}>
                {currentOnsiteIn?.image && (
                  <View
                    key={currentOnsiteIn?.image}
                    style={{
                      width: width / 2,
                      // height: width / 2,
                      backgroundColor: Color.White,
                    }}>
                    <TextCM
                      style={{
                        fontSize: 18,
                        marginBottom: 10,
                        fontFamily: Font.InterSemiBold600,
                      }}>
                      {t('DB.etms.checkImage', {type: 'in'})}
                    </TextCM>
                    {loadingImgIn && (
                      <ActivityIndicator
                        style={StyleSheet.absoluteFill}
                        size="small"
                        color="#999"
                      />
                    )}
                    <Image
                      width={width / 2 - 16}
                      height={width / 2 - 16}
                      borderRadius={25}
                      source={{
                        uri: currentOnsiteIn?.image,
                      }}
                      onLoadStart={() => setLoadingImgIn(true)}
                      onLoadEnd={() => setLoadingImgIn(false)}
                    />
                  </View>
                )}
                <View style={{width: width / 2 - 32}}>
                  {currentOnsiteIn?.location && (
                    <TextCM style={{marginTop: 32}}>
                      {t('DB.etms.checkLocation', {type: 'in'})}{' '}
                      {currentOnsiteIn?.location}
                    </TextCM>
                  )}
                </View>
              </View>
            )}
            {currentOnsiteOut?.image && (
              <View
                key={currentOnsiteOut?.image}
                style={{flexDirection: 'row', gap: 16, marginTop: 16}}>
                {currentOnsiteOut?.image && (
                  <View
                    style={{
                      width: width / 2,
                      // height: width / 2,
                      backgroundColor: Color.White,
                    }}>
                    <TextCM
                      style={{
                        fontSize: 18,
                        marginBottom: 10,
                        fontFamily: Font.InterSemiBold600,
                      }}>
                      {t('DB.etms.checkImage', {type: 'out'})}
                    </TextCM>
                    {loadingImgOut && (
                      <ActivityIndicator
                        style={StyleSheet.absoluteFill}
                        size="small"
                        color="#999"
                      />
                    )}
                    <Image
                      width={width / 2 - 16}
                      height={width / 2 - 16}
                      borderRadius={25}
                      source={{
                        uri: currentOnsiteOut?.image,
                      }}
                      onLoadStart={() => setLoadingImgOut(true)}
                      onLoadEnd={() => setLoadingImgOut(false)}
                    />
                  </View>
                )}
                <View style={{width: width / 2 - 32}}>
                  {currentOnsiteOut.location && (
                    <TextCM style={{marginTop: 32}}>
                      {t('DB.etms.checkLocation', {type: 'out'})}{' '}
                      {currentOnsiteOut?.location}
                    </TextCM>
                  )}
                </View>
              </View>
            )}
          </ScrollView>
        </View>
      );
    }
  };

  const getFuncCheckInOut = () => {
    if (type === 'checkinout') {
      checkinoutDEFAULT();
    } else if (type === 'onsite') {
      checkinoutOnsite();
    }
  };

  const checkinoutDEFAULT = async () => {
    try {
      setIsLoading(true);
      const data = {
        ...dataLocal,
        latitude: dataLocal?.latitude?.toString() || '',
        longitude: dataLocal?.longitude?.toString() || '',
        timestamp: Date.now(),
        ssid: ip?.ssid || 'FIS-GUEST',
        ipGateway: ip?.ipGateway || '198.18.0.5',
        deviceId: devicesInfo?.deviceId,
        deviceName: devicesInfo?.deviceName,
        signature: '', // HMAC-SHA256
      };
      const signature = CommonUtils.getSignature(data, dataAccount?.id);
      data.signature = signature;
      let res,
        typeCheck = 'in';
      if (stateInOut?.dataIn) {
        typeCheck = 'out';
        res = await AccountServices.checkoutTHADS(data);
        if (res) {
        }
      } else {
        res = await AccountServices.checkinTHADS(data);
      }
      if (res) {
        switch (res.code) {
          case 'SUCCESS':
            getDataCheckin();
            Toast.show({
              type: 'success',
              text1: t('DB.etms.checkSuccess', {
                type: typeCheck === 'in' ? 'in' : 'out',
              }),
            });
            break;
          case 'ERROR_INVALID_TIME':
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('DB.etms.errorTimeCheckin'),
            });
            break;
          case 'ERROR_NETWORK_ISSUE':
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('DB.etms.errorConnection'),
            });
            break;
          case 'ERROR_INVALID_SIGNATURE':
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('DB.etms.errorInvalidSignature'),
            });
            break;
          case 'ERROR_DEVICE_ID_MISSING':
            getDataCheckin();
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('DB.etms.errorInvalidDevice'),
            });
            break;

          default:
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('CM.error'),
            });
            break;
        }
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.error'),
        });
      }
    } catch (error) {
    } finally {
      setIsLoading(false);
    }
  };

  async function ensureLocationPermission(): Promise<boolean> {
    if (Platform.OS === 'ios') {
      return new Promise(resolve => {
        Geolocation.requestAuthorization();
        Geolocation.getCurrentPosition(
          () => resolve(true),
          err => {
            console.error(err);
            resolve(false);
          },
        );
      });
    } else {
      const status = await PermissionsAndroid.check(
        PermissionsAndroid.PERMISSIONS.ACCESS_FINE_LOCATION,
      );
      if (status) return true;
      const result = await PermissionsAndroid.request(
        PermissionsAndroid.PERMISSIONS.ACCESS_FINE_LOCATION,
      );
      return result === PermissionsAndroid.RESULTS.GRANTED;
    }
  }

  const checkinoutOnsite = async () => {
    // return Linking.openSettings();
    try {
      const couldGetLocation: boolean = await ensureLocationPermission();
      if (!couldGetLocation) {
        setLocationAccess(true);
      } else {
        let image;
        try {
          image = (await CropPickerComponent.TakePhoto({
            cropping: false,
            includeBase64Enabled: false,
            useFrontCameraEnabled: true,
          })) as any;
        } catch (error) {
          console.error('error', error);
          if (
            typeof error === 'string' &&
            error.includes('camera permission')
          ) {
            displaySnackbar(
              'Yêu cầu quyền truy cập camera để checkin',
              'error',
            );
            CommonUtils.openPermissionSettings();
          } else {
            displaySnackbar(t('CM.error'), 'error');
          }
          return;
        }
        const uri = image?.path.startsWith('file://')
          ? image?.path
          : `file://${image?.path}`;
        const location = await getLocation();
        const request = {
          approvedBy: 'admin@fpt.com',
          approvedUsrId: 2386,
          checkinDate: new Date().toISOString(),
          latitude: (location?.coords as any).latitude.toString(),
          longitude: (location?.coords as any).longitude.toString(),
          type: dataOnsite?.checkinDate ? 'check out' : 'check in',
          reason: 'work onsite',
        };
        const formData = new FormData();
        formData.append('request', JSON.stringify(request));
        formData.append('file', {
          uri: uri,
          type: image.mime,
          name: image.filename || `photo_${Date.now()}.jpg`,
        });
        const dataCheckin = await AccountServices.requestOnsite(formData);
        if (!dataCheckin) {
          displaySnackbar(
            t(
              dataOnsite?.checkinDate
                ? 'DB.etms.checkinOnsiteNotsuccess'
                : 'DB.etms.checkoutOnsiteNotsuccess',
            ),
            'error',
          );
        } else {
          displaySnackbar(
            t(
              dataOnsite?.checkinDate
                ? 'DB.etms.checkinOnsiteSuccess'
                : 'DB.etms.checkoutOnsiteSuccess',
            ),
            'success',
          );
          getDataOnsite();
        }
      }
    } catch (error) {
      displaySnackbar(t('CM.errorConnection'), 'error');
    }
  };

  const displaySnackbar = (
    mess: string,
    type: 'error' | 'success' = 'error',
  ) => {
    setMessSnack(mess);
    setTypeSnack(type);
    setOpenSnackBar(true);
  };

  const renderChildModal = () => {
    switch (showModal) {
      case 'calender':
        return (
          <CalenderDetail
            dataCheckInOut={dataCheckInOut}
            onClose={() => setShowModal(undefined)}
            getDataCheckin={getDataCheckin}
          />
        );
      case 'onsite':
        return (
          <CheckInOut type="onsite" onClose={() => setShowModal(undefined)} />
        );
      default:
        break;
    }
  };

  return (
    <>
      {getHeaderCheckInOut()}
      <View
        style={[
          styles.ctnContainer,
          type === 'onsite' && {backgroundColor: '#fff'},
        ]}>
        <View style={styles.ctnWifi}>
          <View style={{rowGap: 6}}>
            <View style={styles.ctnItemWifi}>
              <Icon
                fill={'#4e1d72'}
                name="wifi-outline"
                width={24}
                height={24}
              />
              <TextCM key={JSON.stringify(ip)} style={styles.txtWifi}>
                {ip?.ssid || '---'}
              </TextCM>
            </View>
            <View style={styles.ctnItemWifi}>
              <Icon
                fill={'#4e1d72'}
                name="calendar-outline"
                width={24}
                height={24}
              />
              <TextCM
                style={{
                  color: '#00204DE5',
                  fontSize: 14,
                  fontFamily: Font.InterRegular400,
                }}>
                {DateUtils.formatDateVietnamese(currentDate)}
              </TextCM>
            </View>
          </View>
          <View style={styles.ctnTime}>
            <Clock date={currentDate} />
            {/* <TextCM style={styles.txtTime}>
              {DateUtils.getCurrentTime(currentDate)}
            </TextCM> */}
          </View>
        </View>

        <View style={styles.ctnBtnCheck}>
          {type === 'onsite' ? (
            <View
              style={{
                ...styles.ctnBtn,
              }}></View>
          ) : (
            <TouchableOpacity
              // disabled={dataCheckInOut}
              style={{
                ...styles.ctnBtn,
                backgroundColor: '#39AC6D',
              }}
              onPress={() => setShowModal('onsite')}>
              <IconOnsite width={20} height={20} />
              <TextCM style={styles.txtBtn}>Onsite</TextCM>
            </TouchableOpacity>
          )}

          {type === 'onsite' ? (
            <TouchableOpacity
              // disabled={!!dataOnsite}
              onPress={getFuncCheckInOut}
              style={{
                ...styles.ctnBtn,
                backgroundColor: '#943CDD',
              }}>
              {/* {!dataOnsite && <Spinner />} */}
              {!isFirstLoadOnsite ? (
                <>
                  <IconCheckin width={20} height={20} />
                  {dataOnsite?.checkinDate ? (
                    <TextCM style={styles.txtBtn}>
                      {t('DB.etms.checkoutOnsite')}
                    </TextCM>
                  ) : (
                    <TextCM style={styles.txtBtn}>
                      {t('DB.etms.checkinOnsite')}
                    </TextCM>
                  )}
                </>
              ) : (
                <Spinner />
              )}
              {/* <IconCheckin width={20} height={20} />
              {dataOnsite?.checkinDate ? (
                <TextCM style={styles.txtBtn}>
                  {t('DB.etms.checkoutOnsite')}
                </TextCM>
              ) : (
                <TextCM style={styles.txtBtn}>
                  {t('DB.etms.checkinOnsite')}
                </TextCM>
              )} */}
            </TouchableOpacity>
          ) : (
            <TouchableOpacity
              disabled={isLoading === true}
              onPress={getFuncCheckInOut}
              style={{
                ...styles.ctnBtn,
                backgroundColor: '#943CDD',
              }}>
              {isLoading && <Spinner />}
              <IconCheckin width={20} height={20} />
              <TextCM style={styles.txtBtn}>
                {!stateInOut?.dataIn
                  ? t('DB.etms.checkin')
                  : t('DB.etms.checkout')}
              </TextCM>
            </TouchableOpacity>
          )}
        </View>

        <View style={[styles.ctnWifi, type === 'onsite' && {marginBottom: 16}]}>
          {!dataOnsite ? (
            <InfoCheckinoutDashboard
              type={type}
              timeCheckinDEFAULT={stateInOut?.dataIn || ''}
              timeCheckoutDEFAULT={stateInOut?.dataOut || ''}
            />
          ) : (
            <InfoCheckinoutDashboard
              type={type}
              timeCheckinONSITE={dataOnsite?.checkinDate || ''}
              timeCheckoutONSITE={dataOnsite?.checkoutDate || ''}
            />
          )}

          <TouchableOpacity
            onPress={() => setShowModal('calender')}
            style={styles.ctnCalendar}>
            <IconCalendar width={20} height={20} />
          </TouchableOpacity>
        </View>

        {getOnsite()}
      </View>
      <ModalFullScreen isVisible={!!showModal} children={renderChildModal()} />

      <ModalConfirmCM
        type="info"
        title={t('DB.etms.permissionLocation')}
        content={t('DB.etms.permissionLocationQuestion')}
        onClose={() => setLocationAccess(false)}
        isVisible={locationAccess}
        labelCancel={t('CM.back')}
        labelConfirm={t('CM.confirm')}
        onConfirm={() => {
          Linking.openSettings();
          CommonUtils.openPermissionSettings('location');
          setLocationAccess(false);
        }}
      />
      <ModalConfirmCM
        type="info"
        title={t('DB.etms.permissionCamera')}
        content={t('DB.etms.permissionCameraQuestion')}
        onClose={() => setShowConfirmOpenCamera(false)}
        isVisible={showConfirmOpenCamera}
        labelCancel={t('CM.back')}
        labelConfirm={t('CM.confirm')}
        onConfirm={() => {
          Linking.openSettings();
          CommonUtils.openPermissionSettings('camera');
          setShowConfirmOpenCamera(false);
        }}
      />
      <Snackbar
        style={{
          backgroundColor: typeSnack === 'success' ? Color.green : Color.Red,
        }}
        visible={openSnackBar}
        onDismiss={() => setOpenSnackBar(false)}
        duration={2000}>
        {messSnack}
      </Snackbar>
    </>
  );
});

export default CheckInOut;
