import React, {useState, useMemo, useEffect, useCallback} from 'react';
import {
  View,
  Text,
  TouchableOpacity,
  Image,
  useWindowDimensions,
  ScrollView,
} from 'react-native';
import {Calendar, Icon, StyleType} from '@ui-kitten/components';
import HeaderCM from '../../../components/Header/HeaderCM';
import TextCM from '../../../components/Text';
import {ICheckInOutRecord} from '../../interfaces';
import {DateUtils} from '../../../../utils';
import {Font} from '../../../../constants';
import {IconEmpty} from '../../../../assets/images';
import {CalendarDateInfo} from '@ui-kitten/components/ui/calendar/type';
import {styles} from './styles';
import {listStatusCheckin} from './helper';
import {useTranslation} from 'react-i18next';
import {useFocusEffect} from '@react-navigation/native';
import Geolocation, {
  GeolocationResponse,
} from '@react-native-community/geolocation';
import {useAccountInfoServices} from '../../../../hooks/apiHooks/useAccountInfoServices';

type Props = {
  onClose?: () => void;
  dataCheckInOut: ICheckInOutRecord[];
  getDataCheckin: (
    d?: Date,
    workLocation?: 'default' | 'onsite' | 'all',
  ) => void;
};

type DayStatus = 0 | 1 | 2;

const CalendarDetail: React.FC<Props> = ({
  onClose,
  dataCheckInOut,
  getDataCheckin,
}) => {
  const {t} = useTranslation();
  const AccountServices = useAccountInfoServices();
  // const {width} = useWindowDimensions();
  const [selectedDate, setSelectedDate] = useState<Date>(new Date());
  const [detailCheckin, setDetailCheckinout] = useState<any>();
  const [currentLatLon, setCurrentLatLon] = useState<{
    lat?: string;
    lon?: string;
  }>({});
  const [disPlayNameLocation, setDisplayNameLocation] = useState('');

  const getCurrentLocation = async (lat?: string, lon?: string) => {
    if (currentLatLon.lat != lat || currentLatLon.lon != lon) {
      const locationCheckin = await AccountServices.fetchLocation({
        lon,
        lat,
      });
      setDisplayNameLocation(locationCheckin?.display_name);
    }
  };

  const dayStatusMap = useMemo<Record<string, DayStatus>>(() => {
    const map: Record<string, DayStatus> = {};
    dataCheckInOut?.forEach(({checkinTime, checkoutTime}) => {
      const date = new Date(checkinTime);
      const key = `${date.getFullYear()}${date.getMonth()}${date.getDate()}`;
      map[key] = checkoutTime ? 2 : (Math.max(map[key] || 0, 1) as DayStatus);
    });
    return map;
  }, [dataCheckInOut]);

  const renderDay = useCallback(
    (info: CalendarDateInfo<Date>, style: StyleType): React.ReactElement => {
      const actualDate = new Date(info.date);
      let key = `${actualDate.getFullYear()}${actualDate.getMonth()}${actualDate.getDate()}`;
      const status: DayStatus = dayStatusMap[key] ?? 0;

      let container = styles.defaultContainer;
      let textStyle = styles.defaultText;
      if (status === 1) {
        container = styles.checkinOnlyContainer;
        textStyle = styles.checkinOnlyText;
      } else if (status === 2) {
        container = styles.bothContainer;
        textStyle = styles.bothText;
      }
      const isSelected =
        actualDate.toDateString() === selectedDate.toDateString();
      return (
        <View
          style={[
            style.style,
            container,
            isSelected && styles.selectedContainer,
          ]}>
          <Text
            style={[
              textStyle,
              isSelected && styles.selectedText,
              info.bounding && {color: '#B0B0B0'},
            ]}>
            {actualDate.getDate()}
          </Text>
        </View>
      );
    },
    [dayStatusMap, selectedDate],
  );
  useFocusEffect(
    useCallback(() => {
      getDataCheckin(new Date());
    }, []),
  );

  const findInfoDay = (day: Date | string = new Date().toUTCString()) => {
    const dateChanged = new Date(day);
    let findData = dataCheckInOut.find(item => {
      const dateCheck = new Date(item.checkinTime);
      return (
        dateCheck.getFullYear() === dateChanged.getFullYear() &&
        dateCheck.getMonth() === dateChanged.getMonth() &&
        dateCheck.getDate() === dateChanged.getDate()
      );
    });

    getCurrentLocation(findData?.latitude, findData?.longitude);
    setDetailCheckinout(findData);
  };

  useEffect(() => {
    findInfoDay();
  }, []);

  const renderAttendanceRecords = (attendenRecord: any, type: 'in' | 'out') => {
    return (
      <View
        // key={index}
        style={[
          styles.attendanceCard,
          type === 'in' ? styles.checkInCard : styles.checkOutCard,
        ]}>
        <Text style={styles.attendanceTitle}>
          {type === 'in'
            ? `Bạn đã check-in thành công lúc: ${DateUtils.getCurrentTime(
                attendenRecord?.checkinTime,
              )}`
            : `Bạn đã check-out thành công lúc: ${DateUtils.getCurrentTime(
                attendenRecord?.checkoutTime,
              )}`}
        </Text>
        <Text style={styles.attendanceDetail}>
          {t('DB.etms.device')}{' '}
          {attendenRecord.deviceName || attendenRecord.deviceId || ''}
        </Text>
        {disPlayNameLocation && (
          <Text key={disPlayNameLocation} style={styles.attendanceDetail}>
            {t('DB.etms.location')} {disPlayNameLocation}
          </Text>
        )}
      </View>
    );
  };

  return (
    <>
      <HeaderCM
        style={{backgroundColor: '#fff'}}
        contentStyle={{backgroundColor: '#fff'}}
        titleStyle={{color: '#000810'}}
        fillIconBackLeft="#000810"
        title={t('DB.etms.historyCheckin')}
        onPressIconLeft={onClose}
      />
      <Calendar
        key={JSON.stringify(dayStatusMap)}
        style={{width: '100%'}}
        date={selectedDate}
        min={new Date(1900, 0, 1)}
        renderDay={renderDay}
        onSelect={D => {
          setSelectedDate(D);
          findInfoDay(D);
        }}
        max={new Date(2300, 0, 1)}
        onVisibleDateChange={(D, CalendarViewMode) => {
          setSelectedDate(D);
          getDataCheckin(D);
        }}
      />

      <ScrollView style={{padding: 16, flex: 1}}>
        <TextCM style={{fontFamily: Font.InterSemiBold600, fontSize: 16}}>
          {DateUtils.formatDateVietnamese(selectedDate)}
        </TextCM>
        {detailCheckin ? (
          <>
            <TextCM
              style={{
                fontSize: 14,
                marginTop: 8,
              }}>
              {detailCheckin.workLocation === 'default'
                ? t('DB.etms.workAtOffice')
                : t('DB.etms.checkinOnsite')}
            </TextCM>
            <Text>
              {detailCheckin.late && detailCheckin.overTime
                ? t('DB.etms.inLateOutEarly')
                : detailCheckin.late
                ? t('DB.etms.inLate')
                : detailCheckin.overTime
                ? t('DB.etms.outEarly')
                : ''}
            </Text>
            {detailCheckin?.checkinTime && (
              <>{renderAttendanceRecords(detailCheckin, 'in')}</>
            )}
            {detailCheckin?.checkoutTime && (
              <>{renderAttendanceRecords(detailCheckin, 'out')}</>
            )}

            {/* <View>
              {detailCheckin?.checkInAttachment && (
                <>
                  <Text
                    style={{
                      fontFamily: Font.InterSemiBold600,
                      fontSize: 16,
                      textAlign: 'center',
                      marginTop: 16,
                    }}>
                    {t('DB.etms.checkImage', {type: 'in'})}
                  </Text>
                  <Image
                    width={width - 16}
                    height={width - 16}
                    borderRadius={25}
                    source={{
                      uri:
                        detailCheckin.checkInAttachment ||
                        'https://upload.wikimedia.org/wikipedia/commons/thumb/b/b3/Blackpink_Ros%C3%A9_Rimowa_1.jpg/500px-Blackpink_Ros%C3%A9_Rimowa_1.jpg',
                    }}
                  />
                </>
              )}
              {detailCheckin?.checkOutAttachment && (
                <>
                  <Text
                    style={{
                      fontFamily: Font.InterSemiBold600,
                      fontSize: 16,
                      textAlign: 'center',
                      marginTop: 16,
                    }}>
                    {t('DB.etms.checkImage', {type: 'out'})}
                  </Text>
                  <Image
                    width={width - 16}
                    height={width - 16}
                    borderRadius={25}
                    source={{
                      uri:
                        detailCheckin?.checkOutAttachment ||
                        'https://upload.wikimedia.org/wikipedia/commons/thumb/b/b3/Blackpink_Ros%C3%A9_Rimowa_1.jpg/500px-Blackpink_Ros%C3%A9_Rimowa_1.jpg',
                    }}
                  />
                </>
              )}
            </View> */}
          </>
        ) : (
          <View
            style={{
              alignItems: 'center',
              justifyContent: 'center',
              flex: 1,
              rowGap: 8,
            }}>
            <IconEmpty width={218} height={149} />
            <TextCM>{t('DB.etms.noInfomation')}</TextCM>
          </View>
        )}
      </ScrollView>
    </>
  );
};

export default CalendarDetail;
